import * as React from "react";
// import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import { Link } from "react-router-dom";
import Paper from "@mui/material/Paper";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import MenuItem from "@mui/material/MenuItem";
import Select from "@mui/material/Select";
import bgImg from "../../img/codes.jpg";
import bgImg2 from "../../img/business.png";
import bgImg3 from "../../img/mentor.jpg";
import { useState } from "react";
import InputLabel from "@mui/material/InputLabel";
const theme = createTheme();

const RequestServiceForm = (props) => {
  const [businessType, setBusinessType] = useState("");
  const [mentorshipType, setMentorshipType] = useState("");
  const [experienceRate, setExperienceRate] = useState("");
  const handleBusinessTypeChange = (e) => {
    setBusinessType(e.target.value);
  };
  const handleMentorshipTypeChange = (e) => {
    setMentorshipType(e.target.value);
  };
  const handleExperienceRateChange = (e) => {
    setExperienceRate(e.target.value);
  };
  const handleSubmit = (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);
    console.log({
      email: data.get("email"),
      password: data.get("password"),
    });
  };

  return (
    <ThemeProvider theme={theme}>
      <Grid container component="main" sx={{ height: "auto" }}>
        <CssBaseline />
        <Grid
          item
          xs={false}
          sm={4}
          md={7}
          sx={{
            backgroundImage: `url(${
              props.serviceType === "custom-software-form"
                ? bgImg
                : props.serviceType === "branding-and-promotion-form"
                ? bgImg2
                : props.serviceType === "mentor-form"
                ? bgImg3
                : ""
            })`,
            backgroundRepeat: "no-repeat",
            backgroundColor: (t) =>
              t.palette.mode === "light"
                ? t.palette.grey[50]
                : t.palette.grey[900],
            backgroundSize: "cover",
            backgroundPosition: "center",
          }}
        />
        <Grid
          item
          xs={12}
          sm={12}
          md={5}
          component={Paper}
          elevation={6}
          square
        >
          <Box
            sx={{
              my: 8,
              mx: 4,
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
            }}
          >
            {/* <Avatar sx={{ m: 1, bgcolor: "secondary.main" }}>
              <LockOutlinedIcon />
            </Avatar> */}
            <Typography component="h1" variant="h5">
              Fill in the form below
            </Typography>
            <Box
              component="form"
              noValidate
              onSubmit={handleSubmit}
              sx={{ mt: 1 }}
            >
              <TextField
                margin="normal"
                required
                autoWidth
                id="name"
                label="Full Name"
                name="name"
                autoComplete="name"
                autoFocus
              />
              <TextField
                margin="normal"
                required
                autoWidth
                id="email"
                label="Email Address"
                name="email"
                autoComplete="email"
              />
              <TextField
                margin="normal"
                required
                autoWidth
                id="phone-number"
                label="Phone Number"
                name="phone-number"
                autoComplete="phone number"
              />
              {props.serviceType !== "mentor-form" ? (
                <TextField
                  margin="normal"
                  required
                  autoWidth
                  id="business-name"
                  label="Busines Name"
                  name="business-name"
                  autoComplete="business name"
                />
              ) : (
                ""
              )}
              {props.serviceType !== "mentor-form" ? (
                <div>
                  <InputLabel id="business-type">Business Type</InputLabel>
                  <Select
                    margin="normal"
                    labelId="business-type"
                    id="business-type"
                    value={businessType}
                    onChange={handleBusinessTypeChange}
                    autoWidth
                    label="Select your Business Type"
                  >
                    <MenuItem value="">
                      <em>None</em>
                    </MenuItem>
                    <MenuItem value={10}>Twenty</MenuItem>
                    <MenuItem value={21}>Twenty one</MenuItem>
                    <MenuItem value={22}>Twenty one and a half</MenuItem>
                  </Select>
                </div>
              ) : (
                <div>
                  <InputLabel id="business-type">Mentorship Type</InputLabel>
                  <Select
                    margin="normal"
                    labelId="mentorship-type"
                    id="mentorship-type"
                    value={mentorshipType}
                    onChange={handleMentorshipTypeChange}
                    autoWidth
                    label="Select your Mentorship Type"
                  >
                    <MenuItem value="">
                      <em>None</em>
                    </MenuItem>
                    <MenuItem value={10}>web</MenuItem>
                    <MenuItem value={21}>mobile</MenuItem>
                    <MenuItem value={22}>graphic design</MenuItem>
                  </Select>
                </div>
              )}
              {props.serviceType !== "mentor-form" ? (
                <TextField
                  margin="normal"
                  required
                  autoWidth
                  id="business-description"
                  label="Business Description"
                  name="business-description"
                  multiline
                  rows={6}
                />
              ) : (
                <div>
                  <InputLabel id="business-type">
                    Rate your experience
                  </InputLabel>
                  <Select
                    margin="normal"
                    labelId="experience-rate"
                    id="experience-rate"
                    value={experienceRate}
                    onChange={handleExperienceRateChange}
                    autoWidth
                    label="Rate your experience"
                  >
                    <MenuItem value="">
                      <em>None</em>
                    </MenuItem>
                    <MenuItem value={20}>0-20%</MenuItem>
                    <MenuItem value={20}>0-40%</MenuItem>
                    <MenuItem value={20}>0-60%</MenuItem>
                    <MenuItem value={20}>0-80%</MenuItem>
                  </Select>
                </div>
              )}

              <Button
                type="submit"
                fullWidth
                variant="contained"
                sx={{ mt: 3, mb: 2 }}
              >
                Submit
              </Button>

              <Link to="/northino-services/">Back</Link>
            </Box>
          </Box>
        </Grid>
      </Grid>
    </ThemeProvider>
  );
};
export default RequestServiceForm;
